/** ----- colors ----- */
.b-cover-page {
  position: fixed;
  z-index: 40;
  top: 0px;
  left: 0px;
  width: 100%;
  min-width: 320px;
  height: 100%;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.3s;
  transition-property: opacity;
  pointer-events: none;
  opacity: 0;
  background-color: #000000;
  touch-action: none; }
  .b-cover-page.active {
    pointer-events: all;
    opacity: .8;
    touch-action: auto; }



