/** ----- colors ----- */
.b-modal__title {
  margin-top: 0px; }

.b-modal__sub-title {
  display: block;
  margin: 1px 0 0;
  font-family: "Roboto", sans-serif;
  font-size: .8em;
  font-weight: 300;
  letter-spacing: normal; }

.b-modal__title-group {
  font-family: "montserratbold";
  font-size: 18px; }

.b-modal__button-wrapper {
  text-align: center; }

.ReactModal__Content {
  top: auto !important;
  right: 0px !important;
  bottom: auto !important;
  left: 0px !important;
  min-height: 100%;
  margin: auto;
  padding: 15px !important;
  border: none !important;
  border-radius: 0 !important;
  box-shadow: 0 2px 6px rgba(40, 36, 35, 0.1); }

.ReactModal__Overlay {
  z-index: 98;
  overflow: auto;
  background-color: rgba(0, 8, 14, 0.85) !important; }

.react_times_button {
  border-radius: 0 !important; }

@media (min-width: 768px) {
  .ReactModal__Content {
    max-width: 520px;
    min-height: 0px;
    margin: 60px auto;
    padding: 30px !important;
    border-radius: 5px !important; } }



