/** ----- colors ----- */
.b-my-booking-card {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-height: 320px;
  padding-left: 15px;
  font-size: 16px;
  line-height: 1.3;
  border-left: 2px solid transparent; }
  .b-my-booking-card.accept {
    border-color: #2a463b; }
  .b-my-booking-card.wait {
    border-color: orange; }
  .b-my-booking-card.cancel {
    border-color: #e92617; }
  .b-my-booking-card__type {
    font-weight: 500; }
  .b-my-booking-card__time {
    font-weight: 500; }
  .b-my-booking-card__playground {
    font-weight: 500; }
  .b-my-booking-card__title {
    margin: 0;
    padding: 0;
    font-weight: 300; }
  .b-my-booking-card__status {
    padding-right: 40px; }
    .accept .b-my-booking-card__status {
      color: #2a463b; }
    .wait .b-my-booking-card__status {
      color: orange; }
    .cancel .b-my-booking-card__status {
      color: #e92617; }
  .b-my-booking-card > div + div {
    margin-top: 15px; }
  .b-my-booking-card__cost-text {
    font-size: 1.06667em;
    font-weight: 500;
    color: var(--accent-text); }
  .b-my-booking-card__button-wrap {
    margin-top: auto !important;
    text-align: right; }



