/** ----- colors ----- */
.b-logotype {
  display: inline-flex;
  align-items: center;
  width: auto;
  height: 64px;
  padding: 12px 16px;
  font-family: "montserratmedium";
  text-align: center;
  color: #ffffff; }
  .no-touchevents .b-logotype:hover,
  .touchevents .b-logotype:active {
    color: #ffffff;
    background: rgba(255, 255, 255, 0.1); }
  .b-logotype__image {
    display: inline-block;
    width: 32px;
    margin-bottom: 1px; }
  .b-logotype__text {
    display: block;
    margin-left: 8px;
    font-size: 21px;
    letter-spacing: .4px; }

@media (min-width: 768px) {
  .b-logotype__image {
    width: 36px;
    margin-bottom: 3px; }
  .b-logotype__text {
    font-size: 29px; } }



