/** ----- colors ----- */
.b-date-calendar {
  padding-top: 10px;
  padding-right: 8px;
  padding-bottom: 20px;
  padding-left: 8px;
  border-top: 1px solid #06281b; }
  .b-date-calendar__calendar {
    width: 100%;
