      color: #ffffff;
      border-color: #ef396f; }
      .b-date-calendar .react-calendar__tile--now.react-calendar__tile--active:enabled:hover, .b-date-calendar .react-calendar__tile--now.react-calendar__tile--active:enabled:focus, .b-date-calendar .react-calendar__tile--now.react-calendar__tile--active {
