/** ----- colors ----- */
.b-slider-schedule {
  position: relative; }
  .b-slider-schedule__header {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    margin-bottom: 15px;
    padding: 0px 45px;
    border-color: #ebebeb;
    border-top: 1px solid;
    border-bottom: 1px solid; }
  .b-slider-schedule__title {
    font-family: "montserratregular";
    font-size: 14px;
    line-height: 17px;
    padding-top: 2px; }
  .b-slider-schedule__slide:focus {
    outline: none; }

.tns-outer .tns-controls {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  height: 30px;
  margin-bottom: -30px; }
  .tns-outer .tns-controls:focus {
    outline: none; }
  .tns-outer .tns-controls button {
    font-size: 16px;
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 30px;
    height: 30px;
    transition: border-color 0.3s;
    color: #8c8c8c;
    border: 1px solid #ebebeb;
    outline: none;
    background: #fff;
    background-color: #ebebeb; }
    .no-touchevents .tns-outer .tns-controls button:not(:disabled):enabled:hover,
    .touchevents .tns-outer .tns-controls button:not(:disabled):enabled:active {
      border-color: #8c8c8c; }
    .tns-outer .tns-controls button:before, .tns-outer .tns-controls button:after {
      position: absolute;
      top: 50%;
      content: '';
      transition: border-color 0.3s;
      transform: translateY(-50%);
      border: 9px solid transparent; }
    .tns-outer .tns-controls button:first-child:after {
      left: 5px;
      border: 7px solid transparent;
      border-right: 7px solid #ebebeb; }
    .tns-outer .tns-controls button:first-child:before {
      left: 0px;
      border-right: 9px solid #8c8c8c; }
    .tns-outer .tns-controls button:last-child:after {
      right: 5px;
      border: 7px solid transparent;
      border-left: 7px solid #ebebeb; }
    .tns-outer .tns-controls button:last-child:before {
      right: 0px;
      border-left: 9px solid #8c8c8c; }
    .tns-outer .tns-controls button:disabled:first-child:before {
      border-right: 9px solid #cccccc; }
    .tns-outer .tns-controls button:disabled:last-child:before {
      border-left: 9px solid #cccccc; }

@media (min-width: 768px) {
  .b-slider-schedule__slide:not(.tns-item) {
    width: 50%;
    padding-right: 10px; }
  .b-slider-schedule__header {
    background-color: #fff;
    border: 1px solid #ebebeb; } }

@media (min-width: 1024px) {
  .b-slider-schedule__slide:not(.tns-item) {
    width: calc(33.3333%);
    padding-right: 13px; } }

@media (min-width: 1280px) {
  .b-slider-schedule__title {
    font-size: 16px; } }

@media (min-width: 1680px) {
  .b-slider-schedule__slide:not(.tns-item) {
    width: calc(100% / 4);
    padding-right: 15px; } }



