/** ----- colors ----- */
.b-schedule-item {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 15px 10px 10px;
  cursor: pointer;
  border-left: 5px solid transparent; }
  .b-schedule-item--free {
    border-left-color: #03ad2d; }
  .b-schedule-item--busy {
    border-left-color: #ff0b0b; }
  .b-schedule-item__time-wrap {
    display: flex;
    align-items: center; }
  .b-schedule-item__time {
    font-size: 18px;
    line-height: 1.4;
    color: #222222; }
    .b-schedule-item__time--finish {
      color: #8c8c8c; }
  .b-schedule-item__info {
    width: 100%; }
  .b-schedule-item__state {
    position: relative;
    padding: 0 15px;
    font-size: 18px;
    line-height: 26px; }
    .b-schedule-item__state:before {
      position: absolute;
      top: 10px;
      left: 0px;
      display: block;
      width: 8px;
      height: 8px;
      content: '';
      border-radius: 50%;
      background-color: transparent; }
  .b-schedule-item__who-tel {
    color: #2a463b; }
  .b-schedule-item__who-name {
    margin-bottom: 2px; }
  .b-schedule-item__court-info {
    font-size: 14px;
    line-height: 18px;
    color: #8c8c8c; }
  .b-schedule-item__click {
    font-weight: 300;
    font-size: 14px;
    line-height: 1.2;
    text-align: right;
    color: #cccccc; }
  .b-schedule-item__name-court {
    margin: 0;
    margin-bottom: 1px;
    font-size: 16px;
    color: #2a463b; }
  .b-schedule-item__address-court {
    margin: 0;
    margin-bottom: 5px; }
  .b-schedule-item__additional-court {
    margin: 0;
    color: #cccccc; }
  .b-schedule-item__cancel {
    width: 38px;
    padding: 10px;
    cursor: pointer;
    background: none;
    border: 0; }

@media (min-width: 768px) {
  .b-schedule-item {
    background-color: #fff;
    cursor: default;
    padding: 10px 30px 10px 25px; }
    .b-schedule-item__time {
      font-size: 22px; }
    .b-schedule-item__info {
      border-bottom: none; }
    .b-schedule-list__item--hover .b-schedule-item {
      cursor: pointer; } }

@media (min-width: 1024px) {
  .b-schedule-item:hover .b-schedule-item__click {
    color: #999; }
  .b-schedule-item__cancel {
    width: 42px; } }

@media (min-width: 1280px) {
  .b-schedule-item__state-name--block {
    display: block; }
  .b-schedule-item__state {
    font-size: 19px; }
    .b-schedule-item__state:before {
      top: 8px; } }



