/** ----- colors ----- */
.b-menu {
  position: fixed;
  z-index: 50;
  top: 0px;
  left: 0px;
  display: flex;
  flex-direction: column;
  width: 90%;
  min-width: 300px;
  max-width: 360px;
  height: 100%;
  font-size: 16px;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
  transition-duration: 0.3s;
  transition-property: transform;
  transform: translateX(-100%);
  background-color: #2a463b; }
  .b-menu.open {
    transform: translateX(0%); }
  .b-menu__header {
    height: 64px;
    border-bottom: 1px solid #02100b; }
  .b-menu__body {
    display: flex;
    flex-direction: column;
    flex: 1 1 auto;
    padding: 15px 15px 15px; }
  .b-menu__list {
    margin: 0;
    padding: 0; }
    .b-menu__list--main {
      font-size: 18px; }
    .b-menu__list + .b-menu__list {
      margin-top: 15px;
      padding-top: 15px;
      border-top: 1px solid rgba(255, 255, 255, 0.5); }
  .b-menu__item {
    list-style-type: none; }
  .b-menu__link {
    display: block;
    padding: 8px 0px;
    line-height: 1; }
    .b-menu__link--disabled {
      cursor: default;
      pointer-events: none;
      color: #ffffff40; }
  .b-menu__social-list {
    margin: auto 0 5px 0;
    padding: 0;
    font-size: 23px;
    text-align: center;
    color: #ffffff40; }
  .b-menu__social-item {
    display: inline-block;
    padding: 10px;
    list-style: none; }
  .b-menu__copyright {
    display: block;
    font-size: 12px;
    text-align: center;
    color: #dddddd; }

@media (min-width: 768px) {
  .b-menu__body {
    padding-left: 30px;
    padding-right: 30px; } }

@media (min-width: 1024px) {
  .b-menu {
    max-width: 420px;
    font-size: 16px; }
    .b-menu__list--main {
      font-size: 19px; }
    .b-menu__copyright {
      font-size: 14px; } }



