/** ----- colors ----- */
.b-preloader {
  position: fixed;
  z-index: 100;
  top: 0px;
  left: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-width: 320px;
  height: 100%;
  background: rgba(0, 0, 0, 0.5); }
  .b-preloader__filter {
    position: absolute;
    visibility: hidden; }
  .b-preloader__dots {
    padding: 30px;
    filter: url(#gooeyness); }
  .b-preloader__dot {
    display: inline-block;
    width: 32px;
    height: 32px;
    margin-right: 20px;
    border-radius: 50%;
    background: #ffffff; }
    .b-preloader__dot:first-child {
      animation: FirstDot 3s infinite; }
    .b-preloader__dot:nth-child(2) {
      animation: SecondDot 3s infinite; }
    .b-preloader__dot:nth-child(3) {
      animation: ThirdDot 3s infinite; }
    .b-preloader__dot:nth-child(4) {
      animation: FourthDot 3s infinite; }
    .b-preloader__dot:nth-child(5) {
      animation: FifthDot 3s infinite; }

@keyframes FirstDot {
  0% {
    transform: scale(1) translateX(0); }
  25% {
    transform: scale(2) translateX(0); }
  50% {
    transform: scale(1) translateX(0); }
  83% {
    transform: scale(1) translateX(240px); }
  100% {
    transform: scale(1) translateX(0); } }

@keyframes SecondDot {
  0% {
    transform: translateX(0px); }
  27% {
    transform: translateX(-40px); }
  50% {
    transform: translateX(0px); }
  81% {
    transform: translateX(180px); }
  100% {
    transform: translateX(0); } }

@keyframes ThirdDot {
  0% {
    transform: translateX(0px); }
  29% {
    transform: translateX(-100px); }
  50% {
    transform: translateX(0px); }
  79% {
    transform: translateX(120px); }
  100% {
    transform: translateX(0); } }

@keyframes FourthDot {
  0% {
    transform: translateX(0px); }
  31% {
    transform: translateX(-160px); }
  50% {
    transform: translateX(0px); }
  77% {
    transform: translateX(60px); }
  100% {
    transform: translateX(0); } }

@keyframes FifthDot {
  0% {
    transform: scale(1) translateX(0); }
  33% {
    transform: scale(1) translateX(-220px); }
  50% {
    transform: scale(1) translateX(0); }
  75% {
    transform: scale(2) translateX(0); }
  100% {
    transform: scale(1) translateX(0); } }



