/** ----- colors ----- */
.b-object-service {
  margin-bottom: 30px;
  padding: 0px; }
  .b-object-service__item {
    position: relative;
    display: flex;
    padding: 6px 0px;
    list-style-type: none;
    border-bottom: 1px solid #ebebeb;
    align-items: center; }
    .b-object-service__item:not(:last-child) {
      margin-bottom: 20px; }
  .b-object-service__training-playground {
    font-size: 16px;
    line-height: 21px;
    color: #2a463b; }
  .b-object-service__playground-additional {
    font-size: 14px;
    line-height: 21px;
    color: #ef396f; }
  .b-object-service__info {
    display: inline-block;
    width: calc(100% - 60px);
    padding-left: 10px;
    vertical-align: top; }
    .b-object-service__info p {
      font-size: 16px;
      line-height: 21px;
      color: #2a463b; }
  .b-object-service__icon {
    display: inline-block;
    width: 60px;
    padding-top: 3px;
    text-align: center;
    vertical-align: top; }
    .b-object-service__icon::after {
      position: absolute;
      top: 6px;
      bottom: 6px;
      left: 60px;
      content: '';
      border-right: 1px solid #ebebeb; }
    .b-object-service__icon svg {
      fill: #f55f0c; }

@media (min-width: 1280px) {
  .b-object-service__training-playground {
    font-size: 17px;
    line-height: 26px; }
  .b-object-service__playground-additional {
    font-size: 16px;
    line-height: 26px; } }



