/** ----- colors ----- */
.b-button {
  display: inline-block;
  padding: 10px 15px;
  font-size: 1rem;
  font-weight: 500;
  text-align: center;
  color: #ffffff;
  border: none;
  border-radius: 5px;
  background: #2a463b;
  transition: .1s; }
  .no-touchevents .b-button:hover,
  .touchevents .b-button:active {
    background-color: #0d6041; }
  .b-button--full {
    width: 100%; }
  .b-button--hollow {
    color: #000;
    border: 1px solid #2a463b;
    background: rgba(255, 255, 255, 0.8); }
    .no-touchevents .b-button--hollow:hover,
    .touchevents .b-button--hollow:active {
      color: #000;
      background-color: rgba(234, 241, 236, 0.9);
      border-color: rgba(186, 199, 189, 0.5); }
  .b-button--save {
    width: 50%;
    margin-right: 10px; }
  .b-button--add-more {
    width: 50%; }
  .b-button--hello-page {
    width: 240px; }
  .b-button--login {
    width: 100%;
    color: #ffffff;
    border: 1px solid #2a463b;
    background: none; }
    .no-touchevents .b-button--login:hover,
    .touchevents .b-button--login:active {
      color: #ffffff;
      background-color: #0d60412c; }
  .b-button--alert-prima {
    min-width: 100px; }

@media (min-width: 1024px) {
  .b-button {
    padding: 12px 24px; }
    .b-button--hello-page {
      width: 260px; } }

@media (min-width: 1280px) {
  .b-button {
    font-size: 16px; } }



