/** ----- colors ----- */
.b-input {
  margin-bottom: 20px; }
  .b-input--inline {
    display: inline-block; }
  .b-input--time-booking {
    display: inline-block;
    width: calc(50% - 10px); }
    .b-input--time-booking:nth-child(2) {
      margin-right: 20px; }
  .b-input__input {
    font-size: 1.06667rem;
    line-height: 1.4;
    background-color: transparent;
    border: none;
    border-bottom: 1px solid #2a463b;
    width: 100%;
    color: #fff;
    height: 40px; }
    .b-input__input:focus {
      outline: none; }
    .b-input__input:disabled {
      border-color: #dddddd;
      opacity: .7; }
  .b-input__label {
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 1.06667rem;
    line-height: 1.4;
    display: inline-block; }
  .b-input--textarea .b-input__input {
    display: block;
    height: 3.5em;
    resize: vertical;
    border-right: 1px solid #2a463b; }
  .b-input--textarea .b-input__label {
    margin-bottom: 15px; }
  .b-input--black-color .b-input__input {
    color: #2c2c2c; }

@media (min-width: 1280px) {
  .b-input__input {
    font-size: 16px; } }



