/** ----- colors ----- */
.b-registration {
  position: absolute;
  top: 0px;
  left: 0px;
  overflow: auto;
  width: 100%;
  min-height: 100vh; }
  .b-registration__bg {
    position: absolute;
    z-index: 1;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    pointer-events: none;
    filter: grayscale(0.1);
    background-color: #1b1c1c; }
  .b-registration .container {
    position: relative;
    z-index: 2;
    display: flex;
    min-height: 100vh;
    padding-top: 100px;
    padding-bottom: 64px; }
  .b-registration__form-wrapper {
    width: 100%;
    max-width: 420px;
    margin: auto; }
  .b-registration__form {
    width: 100%;
    padding: 15px;
    border-radius: 5px;
    background: rgba(18, 18, 18, 0.95); }
  .b-registration__heading {
    text-align: center;
    color: #ffffff; }
  .b-registration__button-wrapper {
    position: relative;
    text-align: center; }
  .b-registration__error {
    width: 100%;
    height: 1.1em;
    margin-top: 20px;
    font-weight: 300;
    line-height: 1.1em;
    text-align: center;
    color: #ff0b0b; }
  .b-registration__sub-navigation {
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-flow: row wrap;
    width: 100%;
    margin-top: 15px;
    padding: 10px 15px;
    font-size: .88em;
    line-height: 2;
    color: #cccccc;
    border-radius: 5px;
    background: rgba(18, 18, 18, 0.8); }
  .b-registration__sub-question {
    margin-right: 10px; }

@media (min-width: 768px) {
  .b-registration__form-wrapper {
    max-width: 460px; }
  .b-registration__form {
    padding: 30px; }
  .b-registration__sub-navigation {
    padding: 15px 30px; } }



