/** ----- colors ----- */
.b-hamburger {
  position: relative;
  z-index: 250;
  left: 0;
  display: inline-flex;
  align-items: center;
  overflow: hidden;
  height: 64px;
  border-left: 1px solid transparent; }
  .no-touchevents .b-hamburger:hover,
  .touchevents .b-hamburger:active {
    color: #ffffff;
    background: rgba(255, 255, 255, 0.1); }
    .no-touchevents .b-hamburger:hover path,
    .touchevents .b-hamburger:active path {
      stroke: #ffffff; }
  .b-hamburger.open {
    background: rgba(255, 255, 255, 0.05);
    border-left: 1px solid #02100b;
    border-bottom: 1px solid #02100b; }
  .b-hamburger.open .b-hamburger__top-bar, .b-hamburger.open .b-hamburger__bottom-bar {
    stroke-dashoffset: -650px; }
  .b-hamburger.open .b-hamburger__middle-bar {
    stroke-dasharray: 0px 220px;
    stroke-dashoffset: -120px; }
  .b-hamburger svg {
    display: block;
    height: 50px;
    margin: 0 -2px 0 -4px;
    transition: none; }
  .b-hamburger path {
    transition: all 0.6s ease-in-out, stroke 0.3s;
    fill: none;
    stroke: #ffffff;
    stroke-linecap: round;
    stroke-width: 40; }
  .b-hamburger__top-bar, .b-hamburger__bottom-bar {
    stroke-dasharray: 240px 910px; }
  .b-hamburger__middle-bar {
    stroke-dasharray: 240px 240px; }

@media (min-width: 1440px) {
  .b-hamburger {
    transition: .3s; }
    .b-hamburger.open {
      border-color: transparent;
      opacity: 0; } }



