/** ----- colors ----- */
.b-list-trainer {
  padding-top: 30px; }
  .b-list-trainer__heading {
    margin-bottom: 15px; }
  .b-list-trainer__note {
    display: block;
    margin: 1px 0 0;
    font-family: "Roboto", sans-serif;
    font-size: .8em;
    font-weight: 300;
    letter-spacing: normal; }
  .b-list-trainer__list {
    margin: 0 -15px;
    padding: 0; }
  .b-list-trainer__item {
    display: block;
    width: 100%; }
    .b-list-trainer__item + .b-list-trainer__item {
      margin-top: 15px; }
  .b-list-trainer__more {
    padding-top: 25px;
    padding-bottom: 35px;
    text-align: center; }

@media (min-width: 768px) {
  .b-list-trainer__list {
    display: flex;
    flex-wrap: wrap;
    margin: 0; }
  .b-list-trainer__item + .b-list-trainer__item {
    margin-top: 15px; } }

@media (min-width: 1024px) {
  .b-list-trainer__list {
    margin: 0 -8px; }
  .b-list-trainer__item {
    width: 50%;
    margin: 0 0 16px;
    padding: 0 8px;
    background: transparent; }
    .b-list-trainer__item + .b-list-trainer__item {
      margin: 0 0 16px; } }

@media (min-width: 1440px) {
  .b-list-trainer__item {
    width: 33.33333%; } }



