/** ----- colors ----- */
.b-schedule {
  background-color: #2a463b; }
  .b-schedule__content {
    overflow: hidden;
    padding: 15px 15px 45px;
    border-radius: 15px 15px 0 0;
    background: #ffffff; }
  .b-schedule__date {
    margin-right: -15px;
    margin-bottom: 20px;
    margin-left: -15px;
    padding: 0 15px 10px;
    font-family: "Roboto", sans-serif;
    line-height: 1;
    text-transform: capitalize;
    border-bottom: 1px solid #ebebeb; }
  .b-schedule__date-top {
    margin-bottom: 5px;
    font-size: 1.1rem;
    font-weight: 300; }
  .b-schedule__date-bottom {
    font-size: 1.3rem; }
  .b-schedule__timetable {
    font-size: 11px;
    font-weight: 300;
    line-height: 14px;
    text-transform: none; }
  .b-schedule__title {
    margin-bottom: 3px;
    font-size: 1rem;
    font-weight: 300;
    color: #2c2c2c; }
    .b-schedule__title--list {
      font-weight: 400; }
  .b-schedule__price-value {
    display: inline-block;
    padding: 0 0 0 4px;
    font-weight: 700;
    color: var(--accent-text); }
  .b-schedule__cost, .b-schedule__playground {
    display: block;
    font-weight: 500; }
  .b-schedule__cost + .b-schedule__cost {
    margin-top: 5px; }
  .b-schedule__price {
    margin-bottom: 15px; }
  .b-schedule__playground {
    margin-bottom: 15px; }
  .b-schedule__playground-name {
    display: inline-block; }
  .b-schedule__playground-address {
    display: inline-block; }
  .b-schedule__playground-item:not(:last-child) {
    margin-bottom: 24px; }
  .b-schedule__not {
    margin-bottom: 50px; }
  .b-schedule__title-note {
    display: block;
    margin: 1px 0 0;
    font-family: "Roboto", sans-serif;
    font-size: .8em;
    font-weight: 300;
    letter-spacing: normal; }

@media (min-width: 768px) {
  .b-schedule__card {
    background: #fff;
    border-radius: 5px;
    padding: 15px;
    box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.145); }
  .b-schedule__content {
    background: #e3e5e8;
    padding: 30px; }
  .b-schedule__date {
    margin-left: -15px;
    margin-right: -15px;
    padding: 0 15px 15px; } }

@media (min-width: 1024px) {
  .b-schedule {
    display: flex;
    width: 100%;
    min-height: 100%;
    margin: 0; }
    .b-schedule__content {
      width: 100%;
      border-radius: 0;
      padding: 30px; }
    .b-schedule__playground {
      margin-bottom: 30px; } }



