/** ----- colors ----- */
.b-booking-request > p {
  margin-bottom: 50px;
  padding: 0px 15px;
  font-family: "montserratbold"; }

.b-booking-request h2 {
  margin-bottom: 30px;
  padding: 0px 15px; }

.b-booking-request__list {
  margin-right: -15px;
  margin-left: -15px; }

.b-booking-request__item {
  padding: 15px;
  background-color: #ffffff;
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.145); }
  .b-booking-request__list--old .b-booking-request__item {
    box-shadow: none;
    background: #ffffffb5; }
  .b-booking-request__item + .b-booking-request__item {
    margin-top: 15px; }

.b-booking-request__player-name {
  font-size: 18px;
  line-height: 26px; }

.b-booking-request__player-tel {
  font-size: 1.1rem;
  color: #2a463b; }

.b-booking-request__training {
  margin-top: 15px;
  margin-bottom: 20px; }

.b-booking-request__training-playground {
  font-size: 16px;
  line-height: 21px;
  color: #2a463b; }

.b-booking-request__training-time {
  font-size: 14px;
  line-height: 21px;
  color: #ef396f; }

.b-booking-request__buttons {
  display: flex;
  flex-flow: row nowrap;
  margin-top: auto; }
  .b-booking-request__buttons > * + * {
    margin-left: 15px; }

@media (min-width: 768px) {
  .b-booking-request__list {
    display: flex;
    flex-flow: row wrap;
    margin: 0 -8px; }
  .b-booking-request__item {
    display: flex;
    flex-direction: column;
    width: calc(50% - 16px);
    margin: 0 8px 16px;
    vertical-align: top;
    border: 1px solid #ebebeb;
    border-radius: 5px;
    background-color: #ffffff; }
    .b-booking-request__item + .b-booking-request__item {
      margin: 0 8px 16px; } }

@media (min-width: 1280px) {
  .b-booking-request__item {
    width: calc(100% / 3 - 16px); }
  .b-booking-request__player-name {
    font-size: 20px;
    line-height: 32px; }
  .b-booking-request__player-tel {
    font-size: 18px;
    line-height: 30px; }
  .b-booking-request__training-playground {
    font-size: 17px;
    line-height: 25px; }
  .b-booking-request__training-time {
    font-size: 16px;
    line-height: 25px; } }



