/** ----- colors ----- */
.b-hello-page {
  margin-top: 60px; }
  .b-hello-page__head {
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 15px;
    font-size: 34px;
    text-align: center; }
  .b-hello-page__logo {
    width: 35px;
    margin-right: 10px;
    margin-bottom: 6px; }
  .b-hello-page__heading {
    margin: auto;
    padding: 10px;
    margin-bottom: 15px;
    font-size: 16px;
    line-height: 1.5;
    text-align: center;
    color: #2c2c2c;
    background: rgba(255, 255, 255, 0.5); }
  .b-hello-page__list {
    margin: 15px 0;
    padding: 0px;
    text-align: center; }
  .b-hello-page__item {
    list-style-type: none; }
    .b-hello-page__item:not(:last-child) {
      margin-bottom: 15px; }

@media (min-width: 768px) {
  .b-hello-page__head {
    font-size: 40px; }
  .b-hello-page__logo {
    width: 40px; }
  .b-hello-page__heading {
    width: max-content;
    font-size: 18px; } }

@media (min-width: 1024px) {
  .b-hello-page__head {
    font-size: 45px; }
  .b-hello-page__logo {
    width: 45px; } }

@media (min-width: 1280px) {
  .b-hello-page__heading {
    font-size: 20px; } }



