/** ----- colors ----- */
.b-checkbox {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 10px; }
  .b-checkbox--hide {
    display: none; }
  .b-checkbox__input {
    display: none; }
  .b-checkbox__label {
    display: inline-block;
    padding-top: 3px;
    cursor: pointer;
    transition: color 0.3s; }
    .b-checkbox--check .b-checkbox__label {
      font-weight: 500; }
  .b-checkbox__check {
    position: relative;
    display: inline-block;
    width: 24px;
    height: 24px;
    margin: 0;
    margin-right: 10px;
    cursor: pointer;
    transition-duration: 0.3s;
    transition-property: background-color;
    transform: translate3d(0, 0, 0);
    -webkit-tap-highlight-color: transparent; }
    .b-checkbox__check svg {
      position: relative;
      z-index: 1;
      transition: all .2s ease;
      transform: translate3d(0, 0, 0);
      fill: none;
      stroke: #2a463b;
      stroke-linecap: round;
      stroke-width: 1;
      stroke-linejoin: round; }
      .b-checkbox__check svg path {
        transition: all .3s linear;
        stroke-dashoffset: 0;
        stroke-dasharray: 60; }
      .b-checkbox__check svg polyline {
        transition: all .2s linear;
        transition-delay: .15s;
        stroke-dashoffset: 66;
        stroke-dasharray: 22; }
    .b-checkbox__check--check svg {
      stroke-width: 2; }
      .b-checkbox__check--check svg path {
        stroke-dashoffset: 60; }
      .b-checkbox__check--check svg polyline {
        stroke-dashoffset: 42; }



