/** ----- colors ----- */
.b-radio {
  position: relative;
  display: block;
  margin-bottom: 10px; }
  .b-radio--disable {
    pointer-events: none; }
  .b-radio__input {
    position: absolute;
    z-index: 0;
    top: 0px;
    left: 0px;
    width: 0px;
    height: 0px;
    opacity: 0; }
  .b-radio__label {
    position: relative;
    display: flex;
    align-items: center;
    flex-direction: row;
    width: auto;
    cursor: pointer; }
    .b-radio__label--checked {
      font-weight: 500; }
  .b-radio__icon {
    position: relative;
    z-index: 1;
    display: inline-block;
    width: 24px;
    height: 24px;
    transition-duration: .15s;
    transition-property: border-color, background-color;
    border: 1px solid #2a463b;
    border-radius: 50%;
    background-color: #ffffff;
    margin-right: 8px; }
    .b-radio__icon:after {
      position: absolute;
      z-index: 2;
      top: 6px;
      left: 6px;
      display: inline-block;
      width: 10px;
      height: 10px;
      content: '';
      transition-duration: .15s;
      transition-property: background-color;
      border-radius: 50%;
      background-color: transparent; }
      .b-radio__label--checked .b-radio__icon:after {
        background-color: #2a463b; }



