/** ----- colors ----- */
.b-add-schedule-card {
  position: relative;
  margin: 0 -15px 15px;
  padding: 15px;
  background: #ffffff;
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.145); }
  .b-add-schedule-card__field--left, .b-add-schedule-card__field--right {
    display: inline-block;
    width: 50%; }
  .b-add-schedule-card__field--left {
    padding-right: 15px; }
  .b-add-schedule-card__field--right {
    padding-left: 15px; }
  .b-add-schedule-card__title-section {
    margin-bottom: 10px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 1.06667rem;
    line-height: 1.4; }
  .b-add-schedule-card__check-list {
    margin: 0;
    padding-left: 0; }
  .b-add-schedule-card__check-item {
    list-style-type: none; }
    .b-add-schedule-card__check-item:not(:last-child) {
      margin-bottom: 24px; }
    .b-add-schedule-card__check-item .b-checkbox {
      margin-bottom: 0; }
  .b-add-schedule-card__delete {
    position: absolute;
    z-index: 1;
    top: 0px;
    right: 0px;
    width: 38px;
    padding: 10px;
    cursor: pointer; }
  .b-add-schedule-card__error {
    margin-top: 15px;
    color: #ff0b0b; }

@media (min-width: 768px) {
  .b-add-schedule-card {
    overflow: hidden;
    margin: 0px 8px 16px;
    padding: 15px;
    border-radius: 5px;
    width: calc(50% - 16px); }
    .b-add-schedule-card__list {
      display: flex;
      flex-flow: row wrap; }
    .b-add-schedule-card__delete {
      top: 0px;
      right: 0px;
      width: 38px;
      padding: 9px;
      border-bottom: 1px solid transparent;
      border-left: 1px solid transparent; }
      .no-touchevents .b-add-schedule-card__delete:hover,
      .touchevents .b-add-schedule-card__delete:active {
        border-color: rgba(0, 0, 0, 0.1); } }



