/** ----- colors ----- */
.b-profile-tabs {
  height: 100%;
  background: #ffffff; }
  .b-profile-tabs__header {
    height: 64px;
    border-bottom: 1px solid #02100b;
    background: #2a463b; }
  .b-profile-tabs__list-wrapper {
    position: fixed;
    z-index: 500;
    top: 0px;
    left: 0px;
    width: 100%;
    min-width: 320px;
    height: 100%;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    transition-duration: 0.3s;
    transform: translateX(-100%);
    opacity: 0;
    background: #e3e5e8; }
    .b-profile-tabs__list-wrapper.open {
      transform: translateX(0%);
      opacity: 1; }
  .b-profile-tabs__list {
    width: max-content;
    max-width: 100%;
    margin: auto;
    padding: 15px 40px 15px 50px; }
  .b-profile-tabs__back {
    position: fixed;
    z-index: 560;
    top: 78px;
    left: 0;
    display: block;
    width: 30px;
    height: 44px;
    padding: 2px 8px 0;
    font-size: 16px;
    font-size: 19px;
    line-height: 44px;
    transition: 0.3s;
    transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
    color: #ffffff;
    border-radius: 0 22px 22px 0;
    background-color: rgba(0, 0, 0, 0.432); }
    .b-profile-tabs__back i.icon {
      transition: 0.3s;
      transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
      transform-origin: center; }
    .b-profile-tabs__back.open {
      width: 40px; }
      .b-profile-tabs__back.open i.icon {
        transform: scale(-1, 1) translateX(-10px); }

@media (min-width: 1024px) {
  .b-profile-tabs {
    display: flex; }
    .b-profile-tabs__header {
      display: none; }
    .b-profile-tabs__list-wrapper {
      position: sticky;
      z-index: 1;
      top: 0px;
      width: 85px;
      min-width: 0;
      height: calc(100vh - 67px);
      margin: 0;
      margin-bottom: 0px;
      padding-top: 60px;
      transform: translateX(0%);
      text-align: center;
      opacity: 1;
      border-right: 1px solid #2a463b;
      border-left: 1px solid #2a463b;
      flex-shrink: 0; }
    .b-profile-tabs__list {
      padding: 0; }
    .b-profile-tabs__content {
      width: 100%; }
    .b-profile-tabs__back {
      display: none; } }

@media (min-width: 1280px) {
  .b-profile-tabs {
    min-height: 100%; } }



