/** ----- colors ----- */
.b-trainer-info {
  padding: 15px;
  height: 100%; }
  .b-trainer-info__playground-list {
    margin-top: 0px;
    margin-bottom: 30px;
    padding-left: 0px; }
  .b-trainer-info__playground-item {
    display: inline-block;
    width: 100%;
    list-style-type: none; }
    .b-trainer-info__playground-item:not(:last-child) {
      margin-bottom: 15px; }
  .b-trainer-info__title-field {
    margin-bottom: 5px;
    font-family: "Roboto", sans-serif;
    font-weight: 500;
    font-size: 1.06667rem;
    line-height: 1.4; }
    .b-trainer-info__title-field--light {
      font-family: "montserratmedium"; }
  .b-trainer-info__cost {
    font-size: 0; }
  .b-trainer-info__button {
    text-align: center; }

@media (min-width: 768px) {
  .b-trainer-info {
    padding: 30px;
    background-color: #e3e5e8; }
    .b-trainer-info__info-wrap {
      display: flex;
      align-items: stretch;
      justify-content: space-between;
      overflow: hidden;
      margin-bottom: 15px;
      border-radius: 5px;
      box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.145);
      margin-left: auto;
      margin-right: auto; }
    .b-trainer-info__form {
      flex: 1 1 auto;
      overflow: hidden;
      margin: 0px auto;
      padding: 30px;
      border-right: 1px solid #ebebeb;
      background-color: #ffffff; }
    .b-trainer-info__playground {
      flex: 1 1 auto;
      min-height: 230px;
      margin: 0px auto;
      padding: 30px;
      border-color: #ebebeb;
      background-color: #ffffff; }
    .b-trainer-info__playground-list {
      margin-right: -7px;
      margin-bottom: 0;
      margin-left: -7px; }
    .b-trainer-info__playground-item {
      padding-right: 7px;
      padding-left: 7px; }
    .b-trainer-info__button {
      padding-bottom: 50px;
      text-align: center; } }

@media (min-width: 1024px) {
  .b-trainer-info__info-wrap {
    margin-bottom: 30px;
    flex-wrap: wrap;
    box-shadow: none;
    margin: 0 -8px 17px; }
  .b-trainer-info__form {
    margin: 0 8px 8px;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.145);
    width: calc(50% - 16px); }
  .b-trainer-info__playground {
    margin: 0 8px 8px;
    border: none;
    border-radius: 5px;
    box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.145);
    width: calc(50% - 16px); }
  .b-trainer-info__playground-item {
    width: calc(100% / 2); } }



