/** ----- colors ----- */
.b-trainer-add-schedule {
  background-color: #2a463b; }
  .b-trainer-add-schedule__calendar {
    z-index: 20; }
  .b-trainer-add-schedule__add-more {
    padding: 5px 0px 30px;
    text-align: center; }
  .b-trainer-add-schedule__save {
    position: fixed;
    z-index: 10;
    bottom: 0;
    left: 0;
    display: flex;
    justify-content: space-between;
    width: 100%;
    min-width: 320px;
    padding: 0 16px 16px; }
  .b-trainer-add-schedule__schedule {
    padding: 15px 15px 65px;
    border-radius: 15px 15px 0 0;
    background-color: #e3e5e8; }

@media (min-width: 768px) {
  .b-trainer-add-schedule__schedule {
    padding: 30px 30px 100px; } }

@media (min-width: 1024px) {
  .b-trainer-add-schedule {
    display: flex;
    min-height: 100%; }
    .b-trainer-add-schedule__calendar {
      margin: 0;
      order: 1;
      flex-shrink: 0;
      padding-top: 78px;
      box-shadow: -2px 0px 3px -2px #c5c5c5; }
      .b-trainer-add-schedule__calendar .b-date-calendar {
        position: sticky;
        top: 0; }
    .b-trainer-add-schedule__schedule {
      width: 100%;
      padding-top: 30px;
      border-radius: 0; }
    .b-trainer-add-schedule__save {
      width: calc(100% - 405px);
      right: auto;
      left: 50%;
      transform: translateX(-50%);
      margin-left: -117px;
      max-width: 1035px; } }



