/** ----- colors ----- */
.b-object-card {
  font-family: "Roboto", sans-serif;
  list-style-type: none;
  background-color: #ffffff;
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.145); }
  .b-object-card__photo-wrapper {
    overflow: hidden;
    height: 160px;
    background-image: linear-gradient(to top, #0ba360 0%, #3cba92 100%); }
  .b-object-card__photo {
    width: 100%;
    height: 100%;
    object-fit: cover; }
  .b-object-card__info {
    padding: 15px;
    line-height: 1.4;
    word-break: break-word; }
  .b-object-card__name-group {
    margin-bottom: 10px;
    padding-bottom: 10px;
    font-size: 1.06667em;
    font-weight: 700;
    line-height: 25px;
    border-bottom: 1px solid #eeeeee; }
  .b-object-card__info-block {
    margin-bottom: 10px;
    font-weight: 500; }
  .b-object-card__title {
    padding: 0;
    font-weight: 300; }
  .b-object-card__cost {
    font-size: 1.06667em;
    font-weight: 500;
    color: var(--accent-text); }
  .b-object-card__list-place {
    padding: 0; }
  .b-object-card__item-place {
    list-style: none; }
  .b-object-card__button-item:not(:last-child) {
    margin-bottom: 10px; }

@media (min-width: 768px) {
  .b-object-card {
    display: flex;
    overflow: hidden;
    flex-direction: row;
    height: 100%;
    min-height: 320px;
    border-radius: 5px; }
    .b-object-card__photo-wrapper {
      flex: 0 0 auto;
      width: 180px;
      height: 100%;
      order: 2; }
    .b-object-card__info {
      display: flex;
      flex-direction: column;
      flex: 1 1 auto;
      padding: 15px; }
    .b-object-card__name-group {
      text-align: center; }
    .b-object-card__button {
      margin-top: auto; } }



