/** ----- colors ----- */
.b-booking-form {
  width: 100%;
  max-width: 600px;
  margin: 0px auto; }
  .b-booking-form__fieldset {
    margin-bottom: 20px;
    padding: 15px;
    border: 1px solid #ebebeb;
    border-radius: 5px; }
    .b-booking-form__fieldset--error {
      border-color: #ff0b0b; }
    .b-booking-form__fieldset > *:last-child {
      margin-bottom: 0; }
  .b-booking-form__note {
    font-size: 1rem;
    margin-bottom: 10px;
    border-left: 2px solid #03ad2d;
    padding-left: 15px; }
  .b-booking-form__error {
    margin-bottom: 20px;
    font-size: 1.2rem;
    color: #ff0b0b; }
  .b-booking-form__button:not(:last-child) {
    margin-bottom: 10px; }

@media (min-width: 768px) {
  .b-booking-form__button {
    width: 50%;
    display: inline-block; }
    .b-booking-form__button:first-child {
      padding-right: 10px; }
    .b-booking-form__button:last-child {
      padding-left: 10px; }
    .b-booking-form__button:not(:last-child) {
      margin-bottom: 0px; } }



