/** ----- colors ----- */
.b-header {
  flex-shrink: 0;
  background-color: #2a463b; }
  .b-header--light {
    z-index: 10;
    background: #00000095; }
  .b-header__wrapper {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    height: 64px; }
  .b-header__get-schedule {
    font-family: "montserratmedium";
    font-size: 16px;
    line-height: 1;
    display: inline-block;
    padding-top: 3px;
    padding-bottom: 3px;
    color: #fff; }
  .b-header__left, .b-header__center, .b-header__right {
    width: auto; }
  .b-header__left {
    text-align: left; }
  .b-header__center {
    text-align: center;
    margin: auto; }
  .b-header__right {
    text-align: right; }

@media (min-width: 768px) {
  .b-header__get-schedule {
    font-size: 16px; } }

@media (min-width: 1024px) {
  .b-header__get-schedule {
    font-size: 18px; } }

@media (min-width: 1680px) {
  .b-header__get-schedule {
    font-size: 20px; } }



