/** ----- colors ----- */
.b-page-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  min-height: 100vh;
  min-width: 320px; }
  .b-page-wrapper.no-scroll, .ReactModal__Body--open .b-page-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    overflow: hidden;
    touch-action: none; }
  .b-page-wrapper.no-touch {
    pointer-events: none;
    touch-action: none; }



