/** ----- colors ----- */
.b-cost-information {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 10px; }
  .b-cost-information--total {
    padding-bottom: 10px;
    border-bottom: 1px solid #ebebeb; }
  .b-cost-information--total .b-cost-information__title {
    font-size: 1.2rem;
    font-weight: 500; }
  .b-cost-information--total .b-cost-information__cost {
    font-size: 1.06667rem;
    font-weight: 500;
    color: var(--accent-text);
    font-size: 1.2rem; }



