/** ----- colors ----- */
.b-profile-tab {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  margin-bottom: 10px;
  padding: 10px;
  list-style-type: none;
  cursor: pointer;
  border: 1px solid #aaaaaa; }
  .b-profile-tab.active .b-profile-tab__text {
    font-weight: 500; }
  .b-profile-tab__icon {
    width: 30px;
    margin-right: 15px;
    flex-shrink: 0; }
  .b-profile-tab__text {
    display: inline-block;
    margin: 0;
    transition: color 0.3s; }

@media (min-width: 1024px) {
  .b-profile-tab {
    margin-bottom: 0;
    padding: 20px 20px;
    transition: 0.15s;
    border: 1px solid transparent;
    border-radius: 0; }
    .no-touchevents .b-profile-tab:hover {
      background: #0b4e3536; }
    .b-profile-tab.active {
      border-color: transparent;
      border-top: 1px solid #2a463b;
      border-right: 0;
      border-bottom: 1px solid #2a463b;
      border-left: 0;
      background-color: #0b4e3536; }
    .b-profile-tab__icon {
      width: auto;
      max-width: 100%;
      margin: 0px; }
    .b-profile-tab__text {
      display: none; } }



